import request from '@/utils/request'


export function get(p) {
    return request({
        url: '/about/get',
        method: 'get',
        params: p
    })
}

export function add(p) {
    return request({
        url: '/about/add',
        method: 'get',
        params: p
    })
}

export function del(p) {
    return request({
        url: '/about/del',
        method: 'get',
        params: p
    })
}


export function update(p) {
    return request({
        url: '/about/update',
        method: 'get',
        params: p
    })
}
export function detail(p) {
    return request({
        url: '/about/detail',
        method: 'get',
        params: p
    })
}

export function getActive(p) {
    return request({
        url: '/about/getActive',
        method: 'get',
        params: p
    })
}
export function active(p) {
    return request({
        url: '/about/active',
        method: 'get',
        params: p
    })
}


