<template>
  <div>
    <flatGreyHead></flatGreyHead>

    <nav-bar cur="6"></nav-bar>

    <div class="container">
      <!-- 预览弹窗 -->
      <el-dialog :visible.sync="show.view" width="90%" :title="v.ed.title">
        <div v-html="v.viewContent"></div>
      </el-dialog>

      <div v-if="!show.addBlog">
        <!-- 显示激活的about -->
        <div v-html="v.activeHTML"></div>

        <el-divider>谢谢</el-divider>
        <br />
      </div>

      <!-- 如果是编辑 -->
      <div v-if="show.addBlog">
        <tinymce v-model="v.editorContent"></tinymce>
        <el-divider></el-divider>
        <el-input v-model.trim="v.title" placeholder="备注" :maxlength="200"></el-input>
        <el-divider></el-divider>

        <el-row>
          <el-col :offset="8" :span="4">
            <el-button type="primary" @click="addAbout('submit')">写好了</el-button>
          </el-col>
          <el-col :span="4">
            <el-button @click="addAbout('cancel')">取消</el-button>
          </el-col>
        </el-row>
      </div>

      <!-- 所有about列表 -->
      <div v-show="!show.addBlog">
        <el-button @click="addAbout('show')" size="mini">添加新about</el-button>
        <el-table :data="list.abouts">
          <!-- 标题 -->
          <el-table-column label="标题" :show-overflow-tooltip="true">
            <template slot-scope="s">
              {{s.row.title}}
              <el-tag v-if="s.row.isActive" type="danger" size="mini">已激活</el-tag>
            </template>
          </el-table-column>

          <!-- 创建时间 -->
          <el-table-column label="创建时间" :show-overflow-tooltip="true">
            <template slot-scope="s">{{fdate(parseInt(s.row.createTime))}}</template>
          </el-table-column>

          <!-- 操作按钮 -->
          <el-table-column label="操作" :show-overflow-tooltip="true">
            <template slot-scope="s">
              <!-- <el-button-group> -->
              <!-- <i @click="viewAbout(s.row.id)" class="el-icon-view"></i>
              <i @click="eidtAbout(s.row.id)" class="el-icon-edit"></i>-->
              <el-button
                icon="el-icon-edit"
                @click="eidtAbout(s.row.id)"
                size="mini"
                type="success"
              >编辑</el-button>

              <el-button
                icon="el-icon-delete"
                @click="delAbout(s.row)"
                size="mini"
                type="warning"
              >删除</el-button>

              <!-- </el-button-group> -->
            </template>
          </el-table-column>

          <!-- 激活操作 -->
          <el-table-column label="激活">
            <template slot-scope="s">
              <el-button
                type="primary"
                @click="enableAbout(s.row.id)"
                size="mini"
                v-if="!s.row.isActive"
              >激活</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import navBar from "@/components/header/navBar";
import flatGreyHead from "@/components/header/flatGreyHead";
import tinymce from "@/components/Tinymce/index";

import { get, add, del, update, detail, active, getActive } from "@/api/about";
export default {
  created() {
    this.getActiveAbout();
    this.getAbouts();
  },

  methods: {
    enableAbout(id) {
      active({ id: id }).then(r => {
        this.getAbouts();
      });
    },
    delAbout(about) {
      if (about.active) {
        this.$message({
          message: "被激活的about无法删除，请先更换激活的about再删除",
          type: "error"
        });
        return false;
      }
      let id = about.id;
      this.$confirm("确定要删除吗", "提示", {
        confrimButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        del({ id: id }).then(r => {
          if (r.code == 200) {
            this.$message({ message: "删除成功", type: "success" });
            this.getAbouts();
          }
        });
      });
    },
    viewAbout(id) {
      getAboutById({ id: id }).then(r => {
        if (r.code == 200) {
          this.show.view = !0;
          this.v.viewContent = decodeURIComponent(r.data.content);
          this.v.ed.title = r.data.title;
        }
      });
    },
    eidtAbout(id) {
      detail({ id: id }).then(r => {
        if (r.code == 200) {
          this.show.addBlog = !0;
          this.v.editorContent = decodeURIComponent(r.data.content);
          this.v.title = r.data.title;
          this.v.aboutId = id;
        }
      });
    },
    getAbouts() {
      get().then(r => {
        if (r.code == 200) {
          r.data.content.forEach(element => {
            element.content = decodeURIComponent(element.content);
          });
          this.list.abouts = r.data.content;
        }
      });
    },
    // 添加about
    addAbout(t) {
      if (t == "show") {
        this.show.addBlog = !0;
  
        this.v.aboutId=""
        this.v.editorContent=""
        this.v.title=''
      }

      if (t == "cancel") {
        this.show.addBlog = !1;
          this.v.aboutId=""
        this.v.editorContent=""
        this.v.title=''
      }
      if (t == "submit") {
        // 如果是编辑
        if (this.v.aboutId) {
          update({
            content: encodeURIComponent(this.v.editorContent),
            title: this.v.title,
            isActive: false,
            id:this.v.aboutId
          }).then(r=>{
            this.show.addBlog = false;
              this.getAbouts();
          });
        } else {
          // 如果是新建
          add({
            content: encodeURIComponent(this.v.editorContent),
            title: this.v.title,            
            isActive: false
          }).then(r => {
            if (r.code == 200) {
              this.show.addBlog = false;
              this.getAbouts();
            }
          });
        }
      }
    },
    getActiveAbout() {
      getActive().then(r => {
        if (r.code == 200) {
          // 如果没有about信息
          if (!r.data.length) {
          } else {
            this.v.activeHTML = decodeURIComponent(r.data[0].content || "");
          }
        }
      });
    }
  },

  data() {
    return {
      list: {
        abouts: []
      },
      show: {
        addBlog: false,
        view: !1
      },
      v: {
        activeHTML: "",
        editorContent: "",
        title: "",
        aboutId: "",
        ed: {
          id: "",
          content: "",
          title: ""
        }
      }
    };
  },
  components: {
    navBar,
    flatGreyHead,
    tinymce
  }
};
</script>

<style scoped>
.container {
  padding-left: 150px;
  padding-right: 150px;
  padding-top: 100px;
}
</style>





